.wrapper {
  font-family: "Rubik", "Arial", sans-serif;
  overflow-x: hidden;
  min-width: 320px;
  width: 100%;
  position: relative;
  display: grid;
  grid-template-columns: 20% 80%;
  min-height: 100vh;
  min-height: calc(100 * var(--vh, 1vh));
}

.wrapper__content {
  display: flex;
  flex-direction: column;
}
.wrapper__content .main-header,
.wrapper__content .main-footer {
  flex-shrink: 0;
}
.wrapper__content .main-index {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.main-index {
  background-color: #f5f8ff;
}

.main-index__container {
  margin: auto auto auto 0;
}

.main-nav {
  background-color: #5e80ff;
  padding-top: 100px;
  padding-bottom: 100px;
  display: flex;
  align-items: center;
}

.main-nav__container {
  width: 220px;
  height: 70%;
  margin: auto 30px auto auto;
}

.main-nav__title {
  user-select: none;
  width: 100%;
  display: inline-block;
  font-size: 40px;
  font-weight: 500;
  color: #ffffff;
  text-transform: uppercase;
  padding-bottom: 10px;
  border-bottom: 2px solid #ffffff;
  margin-bottom: 15px;
}

.main-nav__list {
  display: grid;
  row-gap: 10px;
}

.main-nav__link {
  transition: 0.3s;
  font-size: 18px;
  text-decoration: none;
  color: #f5f8ff;
  text-transform: uppercase;
}

.main-nav__link:hover {
  text-shadow: 0 0 5px #ffffff;
}

.main-nav__link:active {
  text-shadow: 0 0 10px #ffffff;
}

.main-header {
  width: 100%;
  border-bottom: 1px solid #D8E3FE;
}

.main-header__container {
  margin: auto auto auto 0;
  padding: 20px 86px;
}

.main-header__container form {
  width: fit-content;
  margin: 0 auto;
}

.main-header__container input {
  transition: 0.6s;
  color: #5e80ff;
  border: none;
  width: 300px;
  background-color: #f5f8ff;
  padding: 10px;
  border-radius: 5px;
  box-shadow: 3px 3px 5px rgba(94, 128, 255, 0.2);
}

.main-header__container input:hover {
  box-shadow: 3px 3px 10px rgba(94, 128, 255, 0.5);
}

.main-header__container input:focus-visible {
  outline: 1px #5e80ff auto;
}

.main-header__container input::placeholder {
  color: #5e80ff;
}

.people__table {
  display: flex;
  flex-wrap: wrap;
}

.people__row,
.people__main {
  width: 900px;
  position: relative;
  display: grid;
  grid-template-columns: 2fr 2fr 2fr 3fr;
}

.people__main {
  background-color: #5e80ff;
  color: #f5f8ff;
}

.people__main .people__item {
  font-weight: 700;
  font-size: 25px;
  color: #f5f8ff;
}

.people__row:nth-of-type(2n+1) {
  background-color: #ffffff;
}

.people__row:nth-of-type(2n+1) .people__item {
  border-right: 1px solid #f5f8ff;
}

.people__row:nth-of-type(2n+1) .people__item:nth-of-type(4n) {
  border-right: none;
}

.people__row:nth-of-type(2n) {
  background-color: #f5f8ff;
}

.people__row:nth-of-type(2n) .people__item {
  border-right: 1px solid #ffffff;
}

.people__row:nth-of-type(2n) .people__item:nth-of-type(4n) {
  border-right: none;
}

.people__item {
  max-width: 200px;
  color: #5e80ff;
  padding: 10px;
}

.people__item:nth-of-type(4n) {
  max-width: 300px;
}

.people__button-inner {
  position: absolute;
  right: -20px;
  top: 50%;
  transform: translate(50%, -50%);
}

.people__button {
  cursor: pointer;
  transition: 0.3s;
  padding: 0;
  width: 20px;
  height: 5px;
  background-color: #5e80ff;
  border-radius: 3px;
  border: none;
}

.people__button:hover {
  box-shadow: 0 0 5px #5e80ff;
}

.people__button:active {
  background-color: #ffffff;
}

.people__container form {
  display: grid;
  grid-template-columns: 2fr 2fr 2fr 3fr;
}

.people__form-buttons {
  margin-top: 15px;
  grid-column: -1/1;
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.people__form-button {
  transition: 0.3s;
  cursor: pointer;
  user-select: none;
  padding: 10px;
  font-weight: 500;
  border: 2px solid #5e80ff;
  border-radius: 5px;
  background-color: #ffffff;
  color: #5e80ff;
}

.people__form-button:hover {
  box-shadow: 0 0 10px #5e80ff;
}

.people__form-button:active {
  background-color: #f5f8ff;
}

.people__input {
  min-height: 60px;
}

.people__input input,
.people__input select,
.people__input textarea {
  resize: none;
  color: #5e80ff;
  padding: 10px;
  width: 100%;
  min-height: 60px;
  box-sizing: border-box;
  border: none;
  background-color: transparent;
  border: 1px solid #5e80ff;
}

.people__input option {
  padding: 10px;
  color: #5e80ff;
  border-radius: 5px;
  border: 1px solid #5e80ff;
}

.people__input input:focus-visible,
.people__input select:focus-visible,
.people__input textarea:focus-visible {
  outline: 1px #5e80ff auto;
}

.people__input label {
  width: 0;
  height: 0;
}

.main-footer {
  border-top: 1px solid #D8E3FE;
}

.main-footer__container {
  margin: auto auto auto 0;
  padding: 20px 86px;
  display: flex;
  justify-content: flex-end;
}

.main-footer__logo {
  user-select: none;
  cursor: default;
  font-weight: 300;
  text-decoration: none;
  text-transform: uppercase;
  color: #5e80ff;
  opacity: 0.7;
}